import localforage from 'localforage'
var store = localforage.createInstance({
  name: "router"
});

export default {
  store: store,
  add: function (name, value) {
    return store.setItem(name, value);
  },
  del: function (name) {
    store.removeItem(name)
  },
  list: function () {
    var array = [];
    let promise = store.iterate(function (value, key, num) {
      array.push({path: key, name: value})
    }).then(function () {
      return new Promise(function (resolve) {
        resolve(array)
      })
    })
    return promise;
  }
}



