<template>
  <div>
    component name:
    <select v-model="componentName">
      <option value="a">a</option>
      <option value="b">b</option>
      <option value="hello">hello</option>
    </select>
    <button @click="add">add</button>
    <ul>
      <li v-for="com in componentNames">
        <dynamic-component :name="com"></dynamic-component>
      </li>
    </ul>
  </div>
</template>
<style scoped>

</style>
<script>
  export default{
    data(){
      return {
        componentName: '',
        componentNames: []
      }
    },
    methods: {
      add(){
        this.componentNames.push(this.componentName);
      }
    },
    components: {}
  }
</script>



