// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'

import VueRouter from 'vue-router'
Vue.use(VueRouter)


import A from './components/A.vue'
import B from './components/B.vue'
import Hello from './components/Hello.vue'
let component = {
  a: A,
  b: B,
  hello: Hello
};

Vue.component('dynamicComponent', {
  props: ['name'],
  render: function (h) {
    let com = h(component[this.name], {})
    return h('div', {},
      [com])
  },
});

const routes = [
  {path: '/', component: require('./DynamicRouterExample.vue')},
  {path: '/component', component: require('./DynamicComponentExample.vue')},
]
function initRoutes(dynamicRoutes) {
  dynamicRoutes.forEach(item => {
    try {
      routes.push({path: '/' + item.path, component: component[item.name]})
    } catch (e) {
      console.error(`component message:${JSON.stringify(item)},error:${e.message}`)
    }
  })
}
import routerDao from './RouterDao'
routerDao.list()
  .then(initRoutes)
  .then(function () {
    var router = new VueRouter({
      routes
    });
    new Vue({
      router,
    }).$mount('#app')
  })







