<template>
  <div>
    path:<input v-model="path"><br>
    component:
    <select v-model="name">
      <option value="a">a</option>
      <option value="b">b</option>
      <option value="hello">hello</option>
    </select>
    <button @click="add">add</button>
    <ul>
      <li v-for="r in routes">
        <router-link :to="r.path">{{r.path}}</router-link>
      </li>
    </ul>
  </div>
</template>
<style scoped>

</style>
<script>
  import routerDao from './RouterDao'

  export default{
    data(){
      return {
        name: '',
        path: '',
        routes: []
      }
    },
    mounted(){
      routerDao.list().then(routes => {
        this.routes = routes;
      })
    },
    methods: {
      add(){
        routerDao.add(this.path, this.name)
          .then(() => {
            return routerDao.list()
          })
          .then(routes => {
            this.routes = routes;
          })
      },
    },
    components: {}
  }
</script>



